<template>
  <div style="margin: 30px">
    <el-divider></el-divider>
    <el-descriptions
      class="margin-top"
      title="模型实验信息"
      style="margin-top: 25px"
      :column="3"
      :size="size">
      <template slot="extra">
        <router-link
          :to="{ name: '模型实验', params: {}}"
          style="text-decoration: none; color: aliceblue"
          align="right">
          <el-button><i class="el-icon-arrow-left" style="margin-right: 10px"></i>
              返回模型实验</el-button>
        </router-link>
      </template>
      <el-descriptions-item label="实验名称">
          {{ ExperimentbaseInfo.name}}
      </el-descriptions-item>
      <el-descriptions-item label="所处阶段">
          {{ ExperimentbaseInfo.lifecycle_stage}}
      </el-descriptions-item>
      <el-descriptions-item label="工件路径">
          {{ ExperimentbaseInfo.artifact_location }}
      </el-descriptions-item>
    </el-descriptions>
    <el-divider></el-divider>

     <el-collapse v-model="activeNames">
            <el-collapse-item title="描述" name="1">
                <div> 
                    <slot :description="description" class="main-a">
                    <span v-show="!editModelTaskVisible">{{ExperimentbaseInfo.tags[0].value}}</span>
                    <el-input v-model="editModelForm.description" type="textarea" 
                              :autosize="{ minRows: 4, maxRows: 6}"
                              class="modelDescStyle"
                              v-show="editModelTaskVisible">
                    </el-input>
                    <el-button  v-show="!editModelTaskVisible" 
                               class="inline" 
                               icon="el-icon-edit" 
                               type="text"  
                               @click="handleModelDescEdit('index',{description:'description'})" 
                               style="padding: 0px; margin-right: 10px;">
                    </el-button>
                    <br>
                     <span v-show="editModelTaskVisible">
                        <el-button type="text"  @click.native="editModelTaskVisible = false" icon="el-icon-back">取消</el-button>
                        <el-button type="text" @click.native="editSubmit" :loading="editLoading" icon="el-icon-check">保存</el-button>
                    </span>
                   </slot>
                </div>
              </el-collapse-item>
          <el-collapse-item title="模型版本列表" name="2">
              <!--工具条-->
              <el-col :span="24" class="toolbar" style="padding-bottom: 10px;">
                  <div>
                      <el-radio-group v-model="stageStatus">
                          <el-radio-button  @click.native="clickBar(1)" label="All"></el-radio-button>
                          <el-radio-button  @click.native="clickBar(2)" label="Active"></el-radio-button>
                      </el-radio-group>
                  </div>
              </el-col>
              <!--列表-->
              <el-table :data="versionList"
                        highlight-current-row
                        v-loading="listLoading"
                        @selection-change="selsChangeVersionTracking" style="width: 100%;">
                  <el-table-column type="selection" min-width="5%">
                  </el-table-column>
                  <el-table-column prop="version" label="版本" min-width="20%">
                      <template slot-scope="scope">
                          <img v-show="scope.row.status==='READY'" src="@/assets/icon-yes.svg"/>
                          <img v-show="scope.row.status!='READY'" src="@/assets/icon-no.svg"/>
                          &nbsp;
                          <el-icon name="version"></el-icon>
                                      <router-link :to="{ name: '模型版本详情', params: {
                                                        version_id: scope.row.version}}"
                                       style='text-decoration: none;'>{{'Version ' + scope.row.version }}
                          </router-link>
                      </template>
                  </el-table-column>
                  <el-table-column  prop="creation_timestamp" label="注册时间" min-width="20%" sortable show-overflow-tooltip>
                      <template slot-scope="scope">
                        {{scope.row.creation_timestamp | formatDate}}
                      </template>
                  </el-table-column>
                  <el-table-column prop="user_id" label="责任人" min-width="10%">
                  </el-table-column>
                  <el-table-column prop="current_stage" label="所处阶段" min-width="15%">
                      <template slot-scope="scope">
                        <el-tag v-if="scope.row.current_stage==='Staging'" type="warning"><strong>{{scope.row.current_stage}}</strong></el-tag>
                        <el-tag v-if="scope.row.current_stage==='Production'" type="success"><strong>{{scope.row.current_stage}}</strong></el-tag>
                        <el-tag v-if="scope.row.current_stage==='None'" type="info"><strong>{{scope.row.current_stage}}</strong></el-tag>
                        <el-tag v-if="scope.row.current_stage==='Archived'" type="info"><strong>{{scope.row.current_stage}}</strong></el-tag>
                      </template>
                  </el-table-column>
                  <el-table-column prop="description" label="版本描述" min-width="30%">
                  </el-table-column>
              </el-table> <!--工具条-->
              <el-col :span="24" class="toolbar">
                  <el-button type="success" @click="compareVersion" :disabled="this.selsVersionCompare.length===0">Compare</el-button>
                  <el-pagination background layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :page-count="total" style="float:right;">
                  </el-pagination>
              </el-col>
           </el-collapse-item>
       </el-collapse>
  </div>
</template>

<script>

import { test, getExperimentbaseInfo, getModelVersions} from '@/api/api'
// eslint-disable-next-line no-unused-vars
import $ from 'jquery'

export default {
    data () {
        return {
            selsVersionCompare: [],
            versionList: [],
            stageStatus: 'All',
            editLoading:false,
            ExperimentbaseInfo:{},
            editModelTaskVisible: false,
            activeNames: ['2'],
            sels:[],
            total: 0,
            page: 1,
            listLoading: false,
            editModelForm:{
                description: ''
            }
        }
    },
  created () {
    // 如果没有这句代码，select中初始化会是空白的，默认选中就无法实现
    // this.form.Tasks = this.Tasks[0].id
  },
    filters: {
      formatDate: function (value) {
        let date = new Date(value);
        let y = date.getFullYear();
        let MM = date.getMonth() + 1;
        MM = MM < 10 ? ('0' + MM) : MM;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        let h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        let m = date.getMinutes();
        m = m < 10 ? ('0' + m) : m;
        let s = date.getSeconds();
        s = s < 10 ? ('0' + s) : s;
        return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
      }
    },
  methods: {
      // 获取模型版本列表
      getVersionList (stage_btn) {
          this.listLoading = true
          let self = this
          let params = {
              model_name: this.$route.params.model_name,
              page: self.page,
              stage_btn: stage_btn,
              user_key: JSON.parse(sessionStorage.getItem('name')),
          }
          let headers = {
              'Content-Type': 'application/json',
              Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
          }
          getModelVersions(headers, params).then(_data => {
              let { msg, code, data } = _data
              self.listLoading = false
              if (code === '999999') {
                  self.total = data.total
                  self.versionList = data.data
                  console.log(self.versionList,"self.versionList")
              } else {
                  self.$message.error({
                      message: msg,
                      center: true
                  })
              }
          })
      },
      clickBar: function(index){
          if (index===1){
              this.getVersionList('all')
          }else{
              this.getVersionList('active')
          }
      },
      // 显示编辑界面
      handleModelDescEdit: function (index, row) {
          this.editModelTaskVisible = true
          let desc = this.ExperimentbaseInfo.tags[0].value
          // this.editForm = Object.assign({}, row);
      this.editModelForm = Object.assign({}, { description: desc })
    },
      disabledCheckBox(row, index) {
          if (row.is_dir===false) {
              return true
          } else {
              return false
          }
      },

      handleModelOps(command) {
          console.log(command,"command")
           this.$router.push(
               {
                   name: '模型即服务',
                   params:
                   {
                       project_id: this.$route.params.project_id,
                       task_id: this.$route.params.task_id,
                       model_id: command
                   }
               })
          this.$message('click on item ' + command);
          //"handleModelOps(scope.$index,scope.row)"
      },
    // 获取模型任务列表
    _getExperimentbaseInfo () {
      this.listLoading = true
      let self = this
      let params = {
        experiment_id: this.$route.params.experiment_id,
        user_key: JSON.parse(sessionStorage.getItem('name'))
      }

      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      getExperimentbaseInfo(headers, params).then(_data => {
        let { msg, code, data } = _data
        self.listLoading = false
        if (code === '999999') {
            self.ExperimentbaseInfo = data
            //console.log(self.ModelbaseInfo,"ModelbaseInfo")
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
        }
      })
    },
    editSubmit: function(){
        let self = this
        this.editLoading= true
        let param = JSON.stringify({ 
            experiment_id: this.$route.params.experiment_id,
            description: this.editModelForm.description,
            user_key: JSON.parse(sessionStorage.getItem('name'))
        })

      console.log(param, 'dsdds')
      $.ajax({
        type: 'post',
        url: test + '/api/experiments/edit_experiment_description',
        async: true,
        data: param,
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: (data) => {
            // console.log(data,"dddddd")
            this.editLoading= false
            this.editModelTaskVisible = false
            this._getExperimentbaseInfo()
            
        }
      })
        
    },
    // 删除
    handleDel: function (index, row) {
      this.$confirm('确认删除该记录吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        // NProgress.start();
        let self = this
        let params = {
          project_id: Number(this.$route.params.project_id),
          table_name: row.tableName,
          ids: [row.id]
        }
        let headers = {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        }
        console.log(params, 'paramsparamsparamslplp')
        delSampleTable(headers, params).then(_data => {
          let { msg, code } = _data
          if (code === '999999') {
            self.$message({
              message: '删除成功',
              center: true,
              type: 'success'
            })
          } else {
            self.$message.error({
              message: msg,
              center: true
            })
          }
          self.getSampleList()
        })
      })
    },

    handleCurrentChange (val) {
      this.page = val
      this.getVersionList('all')
    },
    selsChangeVersionTracking: function (sels) {
      this.selsVersionCompare = sels
    },
      //比较模型版本信息
      compareVersion: function () {
          let ids = this.selsVersionCompare.map(item => item.id)
          let self = this
          this.$confirm('确认比较选中记录吗？', '提示', {
              type: 'warning'
          }).then(() => {
              self.listLoading = true
              let params = {
                  project_id: Number(this.$route.params.project_id),
                  ids: ids
              }
              let headers = {
                  'Content-Type': 'application/json',
                  Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
              }
              this.$router.push(
                  {
                      name: '模型实验跟踪详情',
                      params:
                      {
                          project_id: this.$route.params.project_id,
                          task_id: this.$route.params.task_id,
                          exp_id: ids[0],
                      },
                      query:{exp_id_list: ids}
                  })
              // eslint-disable-next-line no-undef
          })
      },
  },
  mounted () {
      // this.getVersionList('all')
      this._getExperimentbaseInfo()
  }
}
</script>
